import React, {Component, Fragment} from 'react';
import './style.less';
import boyPng from '../../../../assets/img/boy.png'
import girlPng from '../../../../assets/img/girl.png'
import YuJingChuLi from "./YuJingChuLi";

class Info extends Component {
    render() {
        const studentInfo = this.props.studentInfo || {};
        const avatarPng = studentInfo.gender === ''?boyPng:girlPng;
        return (
            <Fragment>
                <div className={'stuInfoContent'} style={this.props.style}>
                    <div className={'avatarBox'}>
                        <img src={avatarPng} alt=""/>
                        <h4 style={{marginTop:10}}>{studentInfo.name}</h4>
                    </div>
                    <p style={{marginTop:20}}><span>{studentInfo.studentName}</span></p>
                    <p><span>{studentInfo.gender}</span></p>
                    <p><span>{studentInfo.studentNo}</span></p>
                    <p><span>{studentInfo.collegeName}</span></p>
                    <p><span>{studentInfo.majorName}</span></p>
                    <p><span>{studentInfo.className}</span></p>
                    <p><span>{studentInfo.nation}</span></p>
                    <p><span>{studentInfo.birthday}</span></p>
                    <p><span>{studentInfo.politicalStatus}</span></p>
                    <p><span>{studentInfo.nativePlace}</span></p>
                    <p><span>{studentInfo.schoolYear}</span></p>
                    <p><span>{studentInfo.graduateYear}</span></p>
                </div>

                <div>
                    <YuJingChuLi studentNo={studentInfo.studentNo}/>
                </div>
            </Fragment>
        );
    }
}

export default Info;



