import React, {Component} from 'react';
import {Card, Table} from 'antd';


export default class GongNengXiangQing extends Component {


    render () {

        const columns = [
            {
                title: '',
                dataIndex: 'name',
                width: "30%"
            },
            {
                title: '',
                dataIndex: 'status',
                width: "20%",
                render: (text, record) => {
                    return (
                        text === 1
                        ?
                        <span title={text} style={{color:"#F04864"}}></span>
                            :
                            <span title={text} style={{color:"#1890FF"}}></span>
                    )
                }
            },
            {
                title: '',
                dataIndex: 'detail',
                render: (text, record) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            }
        ]

        let title;
        if(this.props.type === 1){
            title = ''
        } else if (this.props.type === 2) {
            title = ''
        } else if (this.props.type === 3) {
            title = ''
        }

        return (
            <Card
                title={<div className="common-title">{title}</div>}
                className='card-noshadow'
                headStyle={{ border: "none"}}
                bodyStyle={{paddingTop:0,paddingBottom:0}}
            >
                <Table
                    dataSource={this.props.liveTable}
                    pagination={false}
                    columns={columns}
                    bordered={true}
                    size="middle"
                />
            </Card>
        )
    }
}


